.burger-btn {
  display: none;
  width: 20px;
  height: 14px;
  margin: 5px 2px 5px auto;
  position: relative;
  cursor: pointer;
}

.burger-btn.active .burger-btn__line {
  width: 20px;
  top: 50%;
  left: 50%;
}

.burger-btn.active .burger-btn__line--1 {
  transform: translate(-50%, -50%) rotate(45deg);
}

/**/

.burger-btn.active .burger-btn__line--2 {
  opacity: 0;
}

.burger-btn.active .burger-btn__line--3 {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.burger-btn__line {
  display: block;
  width: 20px;
  height: 2px;
  background-color: #000;
  border-radius: 2px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.1s linear;
}

.burger-btn__line--1 {
  top: 0%;
}

.burger-btn__line--3 {
  top: 100%;
}

.burger-menu {
  width: 100%;
  height: auto;
  z-index: 11;
  position: absolute;
  top: 68px;
  left: 0;
  border-bottom: 1px solid var(--secondary-light, #eee);
  background: var(--secondary-lightest, #fff);
  visibility: hidden;
  opacity: 0;
  overflow: hidden;
  transform-origin: 0 0;
  transform: rotateX(-90deg);
  transition: 0.2s linear;
}

.burger-menu__inner {
  width: 100%;
  padding: 16px 32px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
}

.burger-menu.active {
  overflow: visible;
  visibility: visible;
  opacity: 1;
  transform: rotateX(0deg);
  position: fixed;
}

.burger-menu .nav {
  display: flex;
  flex-direction: column;
  padding: 8px 0;
  width: 100%;
}

.burger-menu .header__btns {
  display: flex;
  margin: 0;
  width: 100%;
  padding-bottom: 2rem;
}

.burger-menu .header__btns .btn {
  width: calc(50% - 4px);
}

.burger-menu__accordion-items {
  gap: 16px;
}

.burger-menu__accordion-item {
  color: var(--secondary-darkest, #000);
  font-size: 16px;
  font-weight: 600;
  line-height: 120%;
  border: none;
  outline: none;
  background-color: unset;
  padding: 0;
}

.burger-menu__accordion-item:hover {
  color: var(--secondary-darkest, #000);
  text-decoration: none;
}

.burger-menu__accordion-item:focus,
.burger-menu__accordion-item:focus-visible {
  outline: none;
}

.burger-menu__accordion-item[aria-expanded='true'] {
  color: var(--primary-darker, #1874c1);
  text-decoration: none;
}

.burger-menu__accordion-item[aria-expanded='true'] svg {
  fill: var(--primary-darker, #1874c1);
  transform: rotate(180deg);
}

.filter-menu {
  width: 100%;
  height: 100vh;
  z-index: 11;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  border-bottom: 1px solid var(--secondary-light, #eee);
  background: var(--secondary-lightest, #fff);
  visibility: hidden;
  opacity: 0;
  overflow: hidden;
  transform-origin: 0 0;
  transform: rotateX(-90deg);
  transition: 0.2s linear;
}

.filter-menu.active {
  overflow: visible;
  visibility: visible;
  opacity: 1;
  transform: rotateX(0deg);
}

.filter-menu__inner {
  width: 100%;
  height: 100%;
  padding: 0 15px 15px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
}

.filter-menu__controls {
  height: 40px;
}

.filter-menu__close {
  width: 20px;
  height: 20px;
  position: relative;
  cursor: pointer;
}

.filter-menu__close-line {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%) rotate(45deg);
  width: 17px;
  height: 2px;
  border-radius: 4px;
  background: var(--secondary-darkest, #000);
}

.filter-menu__close-line::before {
  position: absolute;
  content: '';
  top: 50%;
  left: 0;
  transform: translateY(-50%) rotate(90deg);
  width: 17px;
  height: 2px;
  border-radius: 4px;
  background: var(--secondary-darkest, #000);
}

