  border-radius: 16px;
}
.form__dropdown .cstm-dropdown__placeholder .clue-btn {
  position: absolute;
  top: 50%;
  left: -8px;
  transform: translateY(-50%);
  border-radius: 100px;
  background: var(--secondary-lightest, #fff);
  box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.16);
  padding: 0;
  border: none;
  pointer-events: all;
}
