  gap: 32px;
  color: var(--secondary-darkest, #000);
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
  margin: 0;
}

.nav__item-link {
  color: inherit;
}

.nav__item-link:hover {
  text-decoration: none;
  color: inherit;
}

.nav__dropdown.show svg {
  transform: rotate(180deg);
}

.nav__dropdown-btn {
  color: var(--secondary-darkest, #000);
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
  padding: 0;
  border: none;
  background: none;
}

.nav__dropdown-items {
  width: max-content;
  padding: 8px;
  border-radius: 16px;
  border: 1px solid var(--secondary-light, #eee);
  background: var(--secondary-lightest, #fff);
  box-shadow: 0px 8px 24px 0px rgba(0, 0, 0, 0.08);
}

.nav__dropdown-item {
  padding: 8px 12px;
  white-space: unset;
  color: var(--secondary-darkest, #000);
  font-size: 14px;
  font-weight: 400;
  line-height: 150%;
}

.nav__dropdown-item:hover {
  border-radius: 8px;
  background: var(--secondary-lighter, #f5f5f5);
}

.nav__dropdown-item:active {
  background-color: unset;
  color: var(--secondary-darkest, #000);
}

.nav .nav__dropdown-item.active {
  color: var(--secondary-darkest, #000);
  background-color: unset;
}

.nav__dropdown:focus,
.nav__dropdown:focus-visible,
.nav__dropdown-btn:focus,
.nav__dropdown-btn:focus-visible,
.nav__dropdown-item:focus,
.nav__dropdown-item:focus-visible {
  outline: none;
}

.nav .dropdown-toggle::after {
  display: none;
}

