.pagination__items {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  padding: 16px 0;
}

.pagination__item {
  display: flex;
  width: 40px;
  height: 40px;
  padding: 10px;
  justify-content: center;
  align-items: center;
  border-radius: 32px;
  border: 1px solid var(--secondary-light, #eee);
