  color: var(--secondary-darkest, #000) !important;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}

.pagination__item:hover {
  text-decoration: none;
}

.pagination__item.active {
  color: var(--secondary-lightest, #fff) !important;
  background: var(--primary-darker, #1874c1) !important;
