  fill: var(--primary-darker, #1874c1);
  transform: rotate(180deg);
}

.pagination .pagination__show-btn:focus,
.pagination .pagination__show-btn:focus-visible,
.pagination .dropdown-item:focus,
.pagination .dropdown-item:focus-visible {
  outline: none;
}

.pagination .dropdown-item:active {
  color: unset;
}

.pagination .dropdown-toggle::after {
  display: none;
}

.pagination .arrow-bottom {
  margin: 0 0 4px 0;
}

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 12;
  background: rgba(0, 0, 0, 0.5);
  transition: opacity 200ms;
  visibility: hidden;
  opacity: 0;
}

.overlay.back {
  background: rgba(0, 0, 0, 0.2);
}

.overlay .cancel {
  position: absolute;
  width: 100%;
  height: 100%;
  cursor: default;
}

.overlay:target {
  visibility: visible;
  opacity: 1;
}

