.timeline {
  max-width: 1270px;
  margin: 0 auto;
  padding: 2.5rem 0 2rem;
}

.timeline h3 {
  margin: 0 0 3rem;
  text-align: center;
}

.timeline__inner {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
}
.timeline__item {
  position: relative;
  flex: 1 0 50%;
  margin-bottom: 45px;
  padding: 30px 15px;
  font-size: 15px;
  line-height: 1;
  text-align: center;
}
.timeline__name,
.timeline__value {
  position: absolute;
  right: 0;
  left: 0;
}
.timeline__name {
  top: 0;
  font-weight: bold;
}
.timeline__value {
  bottom: 0;
}
.timeline__line {
  position: relative;
  height: 2px;
  margin: 0.75rem -15px;
  background: #cecece;
}
.timeline__line::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;

  display: block;
  width: 24px;
  height: 24px;

  border-radius: 50%;
  border: 2px solid #cecece;
  background: #fff;

  transform: translate(-50%, -50%);
}

.timeline__item-next .timeline__line::after,
.timeline__item--completed .timeline__line::after {
  border-color: #1874c1;
  background: #1874c1;
}

.timeline__item-next::after {
  content: '';
  position: absolute;
  top: 50%;
  right: 0;
  display: block;
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 10px solid #1874c1;
  transform: translateY(-50%);
}

@media (min-width: 740px) {
  .timeline__item {
    position: relative;
    flex: 1 0 33.333%;
  }
}

@media (min-width: 910px) {
  .timeline__inner {
    flex-wrap: nowrap;
  }
  .timeline__item {
    flex: 1 0 14%;
    margin-bottom: 0;
    padding: 100px 15px;
  }
  .timeline__line::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;

    display: block;
    width: 1px;
    margin-left: -1px;
    height: 57px;
    border: 1px dashed #1874c1;
  }
  .timeline__info {
    z-index: 2;
    position: absolute;
    bottom: 173px;
    right: 5px;
    left: 5px;
    display: block;
    background: #fff;
  }
  .timeline__item:nth-child(2n) .timeline__line::before {
    top: 0;
    bottom: auto;
  }
  .timeline__item:nth-child(2n) .timeline__info {
    bottom: auto;
    top: 173px;
  }
  .timeline__name,
  .timeline__value {
    position: static;
    right: 0;
    left: 0;
  }
  .timeline__name {
    margin: 0 0 7px;
  }
  .timeline__item-next {
    padding: 15px;
  }
  .timeline__item-next .timeline__line::before {
    content: none;
  }
  .timeline__item-next .timeline__name {
    margin-bottom: 24px;
  }
  .timeline__item-next .timeline__value {
    margin-top: 24px;
  }
}

